.sticky-header {
  position: fixed;
  width:100%;
  top: 0;
  z-index: 100;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #333;
  color: #fff;
  padding: 10px 20px;
}

.logo {
  font-size: 24px;
}

.menu ul {
  list-style: none;
  display: flex;
  padding: 0;
  margin: 0;
}

.menu li {
  margin: 0 20px;
}

.menu a {
  text-decoration: none;
  color: #fff;
  font-weight: bold;
}

.social-media a {
  text-decoration: none;
  color: #fff;
  font-size: 20px;
  margin-left: 20px;
}

